<template>
  <div class="StudentWork">
    <el-table :data="tableData" v-loading="loading" border style="height: 510px">
      <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
      <el-table-column prop="userId" label="所属班级" align="center"></el-table-column>
      <el-table-column prop="title" label="作业名称" align="center"></el-table-column>
      <el-table-column prop="completed_text" label="完成情况" align="center"></el-table-column>
    </el-table>
    <el-card class="card">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page"
          :page-sizes="[5, 10, 20, 30,50]"
          :page-size="size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "StudentWork",
  data(){
    return{
      tableData:[],
      total:0,
      page:1,
      size:10,
      loading:true
    }
  },
  created() {
    this.total = this.tableData.length;
    let that = this;
    setTimeout(() => {
      that.$message({message:'加载完成暂无数据',type:'warning'})
      that.loading=false
    }, 1500);
  }
}
</script>

<style scoped lang="scss">
.StudentWork{
  width: 100%;
  height: 95%;
  margin: 10px;
  .el-card {
    width: 99%;
    height: 100%;
    .el-main{
      height:480px;
    }
  }
}
.el-table{
  width: 99%;
}

</style>